import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { createEmployee } from './employeeAction';

class EmployeeCreator extends Component {
    state = {
        name: '',
        surname: '',
        birthday: '',
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
        console.log([e.target.id], e.target.value)
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.createEmployee(this.state);
        this.props.history.push('/');
    }
    
    render() {
        const { auth } = this.props;
        if (!auth.uid) return <Redirect to='/signIn' />
        return (
            <div className="container">
                <form onSubmit={this.handleSubmit}>
                    <h5>New employee</h5>
                    <div className="input-field">
                        <label htmlFor="name">Name</label>
                        <input type="text" id="name" onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="surname">Surname</label>
                        <input type="text" id="surname" onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="birthday">Birthday</label>
                        <input type="date" id="birthday" onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <button>Create employee</button>
                    </div>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.firebase.auth,
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        createEmployee: (employee) => dispatch(createEmployee(employee))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(EmployeeCreator);