import React from 'react';
import { NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { signOut } from 'components/auth/authActions';

const SignedInLinks = (props) => {
    const { profile } = props;
    console.log(profile)
    return (
        <ul className="links">
            <li className="links__item">
                <NavLink className="link" to="/createEmployee">Create employee</NavLink>
            </li>
            <li className="links__item">
                <NavLink className="link" onClick={props.signOut} to='/'>Log out</NavLink>
            </li>
            <li className="links__item">
                <NavLink className="link" to='/'>{profile.initials}</NavLink>
            </li>
        </ul>
    )
}
const mapDispatchToProps = (dispatch) => {
    return {
        signOut: () => dispatch(signOut())
    }
}

const mapStateToProps = (state) => {
    return {
        profile: state.firebase.profile,
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SignedInLinks);