import React from 'react';
import moment from 'moment';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import { firestoreConnect } from 'react-redux-firebase';


const EmployeeDetails = (props) => {
    const { employee, auth } = props;
    const formatedDate = moment(employee.birthday.toDate().toString()).calendar();
    
    if (!auth.uid) return <Redirect to='/signIn' />

    return (
        <div className="employee-details container">
            <div>First name: {employee.name}</div>
            <div>Last name: {employee.surname}</div>
            <div>Birthday: {formatedDate}</div>
            <div>Creared by: {employee.createdBy}</div>
        </div>
    )
}

const mapStateToProps = (state, ownProps) => {
    const id = ownProps.match.params.id;
    const employees = state.firestore.data.employees;
    const employee = employees && employees[id];
    return {
        employee: employee,
        auth: state.firebase.auth,
    }
}
export default compose(
    connect(mapStateToProps),
    firestoreConnect([
        { collection: 'employees' }
    ])
)(EmployeeDetails)